webpackJsonp([39107087938240],{

/***/ 387:
/***/ (function(module, exports) {

	module.exports = {"data":{"markdownRemark":{"html":"<blockquote>\n<p>本文出自<a href=\"https://overreacted.io/\">overreacted</a>，这是<a href=\"https://mobile.twitter.com/dan_abramov\">Dan Abramov</a>写的博客，我觉得对很有用所以特意做了这个翻译<a href=\"/posts/overreacted\">系列</a>，原文链接请查看<a href=\"https://overreacted.io/my-wishlist-for-hot-reloading/\">这里</a></p>\n</blockquote>\n<p>你可能总是会有个一个项目，让你反复的去思考你的方案的利弊，有时候会停止一段时间，然后继续尝试新的方案 - 如是反复。对于你们，可能是一个路由或者一个虚拟滚动列表。对于我而言，应该算是热更新了</p>\n<p>My first exposure to the idea of changing code on the fly was a brief mention in a book about Erlang that I read as a teenager. Much later, like many others, I fell in love with <a href=\"https://vimeo.com/36579366\">Bret Victor’s beautiful demos</a>. I’ve read somewhere Bret was unhappy with people cherry-picking “easy” parts of his demos and screwing up the big vision. (I don’t know if this is true.) <strong>In either case, to me shipping even small incremental improvements that people take for granted later is a success.</strong> Smarter people than me will work on Next Big Ideas.</p>\n<p>现在我必须要澄清，这篇文章讨论的中没有一个<code class=\"language-text\">想法</code>是出自我本人的。我是被很多项目和开发人员<a href=\"https://redux.js.org/#thanks\">启发</a>的。事实上，很多我没接触过他们项目的人告诉过，我正在抢占和他们做的相似的领域。</p>\n<p>我不是一个发明家。如果我有个”想法“，那一定是有什么启发了我，我只是通过文字、代码、例子来吧这个想法分享给更多的人。</p>\n<p>而 热更新 启发了我</p>\n<hr>\n<p>我已经做了很多尝试来实现React的热更新。</p>\n<p>回想起来，我拼凑出来的[第一个例子]改变了我的人生。也让我有个第一个Twitter关注者，在Github上第一次超过1K星，第一次<a href=\"https://news.ycombinator.com/item?id=8982620\">HN fontage</a>，以及第一次<a href=\"https://www.youtube.com/watch?v=xsSnOQynTHs\">conference talk</a>(在现有的项目中使用Redux)。之后的迭代都工作的非常完美。但是，很快React<em>移除</em>了<code class=\"language-text\">createClass</code>，这让我的热更新功能变的不那么有效。</p>\n<p>之后，我<a href=\"https://medium.com/@dan_abramov/hot-reloading-in-react-1140438583bf?source=user_profile---------6------------------\">尝试了很多</a>来修复这个问题，每个尝试都会存在些许缺陷。其中有一种问题依然存在于ReactNative中(因为我的问题导致热更新函数在ReactNative中不起作用，非常抱歉!)</p>\n<p>我对我因为能力不足或缺少时间而不能处理好一些issues感到很沮丧，我曾将React Hot Loader 交给一些更加有天赋的源码贡献者。他们推动了React Hot Loader的发展并且找到了很多很棒的解决方案来处理我之前设计上的漏洞。我非常感谢他们能够在众多困难面前依然能够保证项目的良好状态。</p>\n<hr>\n<p><strong>说实话，React中的热更新非常有用</strong> 事实上，这篇文章是使用gatsby构建的，gatsby是建立在React Hot Loader上的。我在我的编辑器里编辑这篇文章并保存后，页面不需要刷新就能够更新。这简直是魔法！在某种程度上，我对React Hot Loader的将来的主流使用方式已经可以很放心了。</p>\n<p>但是还是有不少的人觉得React Hot Loader应该还可以做的更好点。甚至有人觉得这只是雕虫小技，这让我觉得有点心碎，但是我觉得他们可能真实想说的是：<strong>使用的体验还不够完美</strong>， 如果总是让用户感觉不到是否热更新起作用了，或者总是异常而开发者却不知道原因，或者非常容易导致页面的刷新，那么这个热更新的功能就没什么用了。我非常赞同，但是对于我来说，这意味着我们还有很多事情要做。接着我就开始兴奋的思考，如果将来React官方来支持热更新会是什么样的。</p>\n<p>(如果你在使用类似于Elm, Reason或者ClojureScript之类的语言，这些问题可能已经在你的原本的生态系统中解决了。我为你的幸福而感到开心。即使这样，我也还是希望能够为Javascript带来类似的功能)</p>\n<hr>\n<p>我想我现在应该要准备另一种方式来实现热更新了，一下是一些原因。</p>\n<p>自从<code class=\"language-text\">createClass</code>这种方式方式来定义组件是个主流的年代的终结，<strong>React Hot Loader的大部分源代码用于处理为组件提供复杂而且容易出错的动态类替换</strong> 如何使用一个新的\"版本\"含有不同函数的类的实例为一个老的已存在的实例打上补丁？最简单的答案大概是 \"替换他的prototype\" 但是即使使用 Proxy，以我的经验来看，这种解决方案还是太粗糙，没有考虑很多边界情况，这种解决方案还不够可靠。</p>\n<p>打个比方，热更新方案非常简单。这里有个Babel插件，它可以将一个函数申明的组件输出成两个函数:</p>\n<div class=\"gatsby-highlight\" data-language=\"jsx\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\"><span class=\"token comment\">// 重新申明最新的版本</span>\nwindow<span class=\"token punctuation\">.</span><span class=\"token function-variable function\">latest_Button</span> <span class=\"token operator\">=</span> <span class=\"token keyword\">function</span><span class=\"token punctuation\">(</span>props<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token comment\">// 你实际的代码被插件移动到了这里</span>\n  <span class=\"token keyword\">return</span> <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>button</span><span class=\"token punctuation\">></span></span>Hello<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>button</span><span class=\"token punctuation\">></span></span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token comment\">// 把这个当成一个\"代理\"</span>\n<span class=\"token comment\">// 这样其他的组件就能够使用了</span>\n<span class=\"token keyword\">export</span> <span class=\"token keyword\">default</span> <span class=\"token keyword\">function</span> <span class=\"token function\">Button</span><span class=\"token punctuation\">(</span>props<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token comment\">// 总是指向最新的组件</span>\n  <span class=\"token keyword\">return</span> window<span class=\"token punctuation\">.</span><span class=\"token function\">latest_Button</span><span class=\"token punctuation\">(</span>props<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>每当这个组件重新编辑后重新编译，<code class=\"language-text\">window.last_Button</code>总是会指向最新的实现，在模块中重用这个<code class=\"language-text\">Button</code>组件让我们能够在不卸载我们的组件的情况下替换组建的实现。</p>\n<p>有很长的一段时间，我觉得我只针对函数实现的热更新功能会误导用户，这可能让用户强行的去写一些函数定义的组件，而不是类定义，这种强行用函数写组件定义的方式，这有可能会产生一些奇怪的代码。但是通过<a href=\"https://reactjs.org/docs/hooks-intro.html\">Hook</a>，函数定义的组件就可以实现几乎所有类定义的组件的需求了，因此我的忧虑也就消除了。下面这种写法的代码通过Hooks也能够\"顺利运行\":</p>\n<div class=\"gatsby-highlight\" data-language=\"jsx\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\"><span class=\"token comment\">// 重新定义最近的版本</span>\nwindow<span class=\"token punctuation\">.</span><span class=\"token function-variable function\">latest_Button</span> <span class=\"token operator\">=</span> <span class=\"token keyword\">function</span><span class=\"token punctuation\">(</span>props<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token comment\">// 你真正的实现代码被放到这里</span>\n  <span class=\"token keyword\">const</span> <span class=\"token punctuation\">[</span>name<span class=\"token punctuation\">,</span> setName<span class=\"token punctuation\">]</span> <span class=\"token operator\">=</span> <span class=\"token function\">useState</span><span class=\"token punctuation\">(</span><span class=\"token string\">'Mary'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n  <span class=\"token keyword\">const</span> <span class=\"token function-variable function\">handleChange</span> <span class=\"token operator\">=</span> e <span class=\"token operator\">=></span> <span class=\"token function\">setName</span><span class=\"token punctuation\">(</span>e<span class=\"token punctuation\">.</span>target<span class=\"token punctuation\">.</span>value<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n  <span class=\"token keyword\">return</span> <span class=\"token punctuation\">(</span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span></span><span class=\"token punctuation\">></span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>input</span> <span class=\"token attr-name\">value</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span>name<span class=\"token punctuation\">}</span></span> <span class=\"token attr-name\">onChange</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span>handleChange<span class=\"token punctuation\">}</span></span> <span class=\"token punctuation\">/></span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>h1</span><span class=\"token punctuation\">></span></span>Hello<span class=\"token punctuation\">,</span> <span class=\"token punctuation\">{</span>name<span class=\"token punctuation\">}</span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>h1</span><span class=\"token punctuation\">></span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span></span><span class=\"token punctuation\">></span></span>\n  <span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token comment\">// 想象这是一个\"代理\"</span>\n<span class=\"token comment\">// 其他的组件会用到这个 that other components would use</span>\n<span class=\"token keyword\">export</span> <span class=\"token keyword\">default</span> <span class=\"token keyword\">function</span> <span class=\"token function\">Button</span><span class=\"token punctuation\">(</span>props<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token comment\">// 总是指向最新的版本</span>\n  <span class=\"token keyword\">return</span> window<span class=\"token punctuation\">.</span><span class=\"token function\">latest_Button</span><span class=\"token punctuation\">(</span>props<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>As long as the Hook call order doesn’t change, we can preserve the state even as <code class=\"language-text\">window.latest_Button</code> is replaced between file edits. And replacing event handlers “just works” too — because Hooks rely on closures, and we replace the whole function.</p>\n<hr>\n<p>这只是其中一种方案的粗糙的描述，还有很多方案。那么我们应该怎么对这些方案做比较呢？</p>\n<p>为了防止我深度研究某一个方案，而这个方案可能在某些地方有坑，<strong>我决定去写下我觉得对判断一个实现组件代码热更新是否是个好的实现的几个重要原则</strong></p>\n<p>将来有可能会把其中的几个原则变成测试用例，这非常棒。这些规则也不是死的，有时候我们可以取折中的方案，但是如果我们的方案非要打破规则，那这个方案一定要是一个成熟的设计方案，而不是灵光一闪。</p>\n<p>这里是我对React组件热更新的期望的原则:</p>\n<h3>正确性</h3>\n<ul>\n<li>\n<p><strong>热更新应该在组件在第一次更新之前是不需要监听的</strong> 在你保存文件之前，代码应该表现的如同热更新没有被启用一样。It’s expected that things like <code class=\"language-text\">fn.toString()</code> don’t match, which is already the case with minification. But it shouldn’t break reasonable application and library logic.</p>\n</li>\n<li>\n<p><strong>热更新不应该打破React的规则</strong> 在交换两个不相干的React节点树的state或者其他的非常规的React操作的时候，组件的生命周期不应该被热更新的操作额外调用。</p>\n</li>\n<li>\n<p><strong>React元素的类型应该始终保持原始的类型</strong>，有一些方案会在组件外层包装一层类型，但是这会打破 <code class=\"language-text\">&lt;MyTing /&gt;.type === MyThing</code>这样的判断。这种常见问题不应该发生。</p>\n</li>\n<li>\n<p><strong>这个方案需要能够简单的支持所有的React type</strong> <code class=\"language-text\">lazy</code>, <code class=\"language-text\">memo</code>, <code class=\"language-text\">forwardRef</code> - 这些都应该被支持，而且这些不需要因为有更多的类型而维护。嵌套的使用比如<code class=\"language-text\">memo(memo(...))</code>也需要能够使用。我们应该在类型改变的时候总是重载这些组件。</p>\n</li>\n<li>\n<p><strong>这个方案不应该重新实现一个React的一个不常用的模块</strong> 和React保持同步很难，如果一个方案重新实现了React，那他可能在长期内都会有问题，比如React添加了一个类似Suspense这样的功能。</p>\n</li>\n<li>\n<p><strong>重新导出模块不应该让热更新失效</strong> 如果一个组件重新将其他模块中的模块重新导出了，这不一样会造成任何问题。</p>\n</li>\n<li>\n<p><strong>静态属性不能失效</strong> 如果你定义了一个<code class=\"language-text\">ProfilePage.onEnter</code>这样的静态函数，你也会希望他在被import整个module的时候也能被读取到，有些框架会依赖这种静态值，所以让这些静态属性的读写正常进行非常重要，而组件自己对这些静态属性的使用也非常重要。</p>\n</li>\n<li>\n<p><strong>我们宁愿丢失一些组件内state，而不是让组件出现异常</strong> 如果我们不能正常的为某些代码(比如，一个类)打补丁，那么最好只是丢失他自己内部的状态。开发者会觉得很奇怪，他们可能会觉得这是一个刷新页面的行为。我们必须要负责的知道哪些是我开门有能力做到的，而剩下的，我们应该放弃。</p>\n</li>\n<li>\n<p><strong>我们宁愿丢失一些组件内的state，而不是使用老版本的state</strong> 这是上面这条原则的特例。比如，一个类不能被热更新，代码需要使用心得组件强制重载这个组件，而不是一直渲染之前没用的东西。</p>\n</li>\n</ul>\n<h3>局部性</h3>\n<ul>\n<li>\n<p><strong>编辑过一个模块后，我们应该竟可能的少重新编译模块</strong> 组件模块在初始化的时候会产生一些副作用，这通常是不允许的。但是你执行的代码量越多，你的逻辑就有可能会越混乱，有时甚至会导致一些逻辑被重复调用。我们正在写的Javascript和React组件现在还是处于一个相对比较不完整的境地，即使是这样，我们也不敢担保我们的做的逻辑没问题。因此，如果我们编辑一个模块，我的热更新应该尽量只更新这个模块。</p>\n</li>\n<li>\n<p><strong>编辑一个组件不应该影响他的父组件和隔壁组件的状态</strong> 这就和<code class=\"language-text\">setState()</code>一样，只会影响自己的子组件的状态，编辑一个组件不应该超过这样的影响力。</p>\n</li>\n<li>\n<p><strong>编辑一个非React的代码的时候应该向上传播的更新</strong> 如果你编辑一个含有纯函数或者常量的文件，假如这个文件被多个组件所引用，应该只更新这些组件，像这些组件丢失一些状态是可以接受的。</p>\n</li>\n<li>\n<p><strong>一个在热更新时抛出运行时的错误不应该被传播</strong> 如果你在写组件的时候产生了错误，这不应该让你的应用崩溃。在React中，通常的解决方案是使用错误边界。但是这对于我们编辑时候无数的错别字而言，是一种比较粗鲁的解决方案啊。我们的热更新需要能够处理一些编译时的错误，让我们在编辑组件的时候，这些错误不会让相邻组件被卸载。但是我们必须保证那些不在热更新产生的错误(用户自己的本来的错误)应该被传播。</p>\n</li>\n<li>\n<p><strong>在开发者确定不需要这个状态之前，我们需要保持组件的状态</strong> 如果你只是在调整样式，如果每次编辑代码都会让组件的状态被重置，这会让人非常不爽。如果是另一种情况，你修改了状态的结构或者初始状态，你会期望这个状态被重置。默认情况下，我们需要尽量去保持组件的状态。但是如果修改状态在热更新的时候产生了异常，这一般标志着有状态可能已经被修改了，所以我们需要重置状态并<em>尝试</em>重新渲染这个组件。注释掉或者注释回来一些代码再编码中非常常见，所以处理好这些也会非常重要。比如，在组件最后删除Hook不应该重置状态。</p>\n</li>\n<li>\n<p><strong>让明确知道用户想要丢弃状态的时候丢弃状态</strong> 有时候我们可以主动的判断用户是否需要重置状态。比如如果Hook的状态被改变了或者一些基本的Hook，比如<code class=\"language-text\">useState</code>改变了初始的状态。我们可以给予用户一个比较简单的注释，当发现这个注释的时候，我们可以在用户的每一次编辑之后强制重置一个组件的状态。比如<code class=\"language-text\">// !</code>或者其他相似的可以方便快速的添加或者取消的注释，这样开发者就能够更专注于调试组建的重载。</p>\n</li>\n<li>\n<p><strong>Support updating “fixed” things.</strong> If a component is wrapped in <code class=\"language-text\">memo()</code>, hot reload should still update it. If an effect is called with <code class=\"language-text\">[]</code>, it should still be replaced. Code is like an invisible variable. Previously, I thought it was important to force deep updates below for things like <code class=\"language-text\">renderRow={this.renderRow}</code>. But in the Hooks world, we rely on closures anyway this seems unnecessary anymore. A different reference should be sufficient.</p>\n</li>\n<li>\n<p><strong>Support multiple components in one file.</strong> It is a common pattern that multiple components are defined in the same file. Even if we only keep the state for function components, we want to make sure putting them in one file doesn’t cause them to lose state. Note these can be mutually recursive.</p>\n</li>\n<li>\n<p><strong>When possible, preserve the state of children.</strong> If you edit a component, it’s always frustrating if its children unintentionally lose state. As long as the element types of children are defined in other files, we expect their state to be preserved. If they’re in the same file, we should do our best effort.</p>\n</li>\n<li>\n<p><strong>Support custom Hooks.</strong> For well-written custom Hooks (some cases like <code class=\"language-text\">useInterval()</code> can be a bit tricky), hot reloading any arguments (including functions) should work. This shouldn’t need extra work and follows from the design of Hooks. Our solution just shouldn’t get in the way.</p>\n</li>\n<li>\n<p><strong>Support render props.</strong> This usually doesn’t pose problems but it’s worth verifying they work and get updated as expected.</p>\n</li>\n<li>\n<p><strong>Support higher-order components.</strong> Wrapping export into a higher-order component like <code class=\"language-text\">connect</code> shouldn’t break hot reloading or state preservation. If you use a component created from a HOC in JSX (such as <code class=\"language-text\">styled</code>), and that component is a class, it’s expected that it loses state when instantiated in the edited file. But A HOC that returns a function component (potentially using Hooks) shouldn’t shouldn’t lose state even if it’s defined in the same file. In fact, even edits to its arguments (e.g. <code class=\"language-text\">mapStateToProps</code>) should be reflected.</p>\n</li>\n</ul>\n<h3>反馈</h3>\n<ul>\n<li>\n<p><strong>Both success and failure should provide visual feedback.</strong> You should always be confident whether a hot reload succeeded or failed. In case of a runtime or a syntax error you should see an overlay which should be automatically be dismissed after it is irrelevant. When hot reload is successful, there should be some visual feedback such as flashing updated components or a notification.</p>\n</li>\n<li>\n<p><strong>A syntax error shouldn’t cause a runtime error or a refresh.</strong> When you edit the code and you have a syntax error, it should be shown in a modal overlay (ideally, with a click-through to the editor). If you make another syntax error, the existing overlay is updated. Hot reloading is only attempted <em>after</em> you fix your syntax errors. Syntax error shouldn’t make you lose the state.</p>\n</li>\n<li>\n<p><strong>A syntax error after reload should still be visible.</strong> If you see a modal syntax error overlay and refresh, you should still be seeing it. It categorically should not let you run the last successful version (I’ve seen that in some setups).</p>\n</li>\n<li>\n<p><strong>Consider exposing power user tools.</strong> With hot reloading, code itself can be your “terminal”. In addition to the hypothetical <code class=\"language-text\">// !</code> command to force remount, there could be e.g. an <code class=\"language-text\">// inspect</code> command that shows a panel with props values next to the component. Be creative!</p>\n</li>\n<li>\n<p><strong>Minimize the noise.</strong> DevTools and warning messages shouldn’t expose that we’re doing something special. Avoid breaking <code class=\"language-text\">displayName</code>s or adding useless wrappers to the debug output.</p>\n</li>\n<li>\n<p><strong>Debugging in major browsers should show the most recent code.</strong> While this doesn’t exactly depend on us, we should do our best to ensure the browser debugger shows the most recent version of any file and that breakpoints work as expected.</p>\n</li>\n<li>\n<p><strong>Optimize for fast iteration, not long refactoring.</strong> This is JavaScript, not Elm. Any long-running series of edits likely won’t hot reload well due to a bunch of mistakes that need to be fixed one by one. When in doubt, optimize for the use case of tweaking a few components in a tight iteration loop rather than for a big refactor. And be predictable. Keep in mind that if you lose developer’s trust they’ll refresh anyway.</p>\n</li>\n</ul>\n<hr>\n<p>This was my wish list for how hot reloading in React — or any component system that offers more than templates — should work. There’s probably more stuff I will add here with time.</p>\n<p>I don’t know how many of these goals we can satisfy with JavaScript. But there’s one more reason I’m looking forward to working on hot reloading again. As an engineer I’m more organized than before. In particular, <strong>I’ve finally learned my lesson to write up requirements like this before diving into another implementation.</strong></p>\n<p>Maybe this one will actually work! But if it doesn’t, at least I’ve left some breadcrumbs for the next person who tries it.</p>","frontmatter":{"title":"翻译：我对热更新的期望","date":"08 December, 2018","tag":"translate,overreacted"}}},"pathContext":{"slug":"translate-overreacted-my-wishlist-for-hot-reloading"}}

/***/ })

});


